
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}



































.kiko-message {
  position: fixed;
  padding: 10px;
  color: #fff;
  background-color: #5F6161;
  border-radius: 4px;
  top:50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 999999999
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity .5s;
  transition: opacity .5s
}
.fade-enter, .fade-leave-to /* .fade-leave-active in below version 2.1.8 */ {
  opacity: 0
}

.kiko-tool-tip {
  display: block;
  position: absolute;
  position: fixed;
  background-color: #3695cc;
  padding: 10px 10px;
  border-radius: 5px;
  color: #fff;
  white-space: nowrap;
  /*    word-wrap: break-word;*/
  z-index: 99999999;
}
.kiko-tool-tip.left {
  -webkit-transform: translate(-100%, -50%);
          transform: translate(-100%, -50%);
}
.kiko-tool-tip.right {
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
}
.kiko-tool-tip.top {
  -webkit-transform: translate(-50%, -100%);
          transform: translate(-50%, -100%);
}
.kiko-tool-tip.bottom {
  -webkit-transform: translate(-50%, 100%);
          transform: translate(-50%, 100%);
}
.kiko-tool-tip.right .arrow {
  display: inline-block;
  position: absolute;
  content: "";
  width: 0;
  height: 0;
  top: 50%;
  left: -10px;
  border-top: 10px solid transparent;
  /*border-left: 10px solid yellow;*/
  border-right: 15px solid #3695cc;
  border-bottom: 10px solid transparent;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
}
.kiko-tool-tip.left .arrow {
  display: inline-block;
  position: absolute;
  content: "";
  width: 0;
  height: 0;
  top: 50%;
  right: -10px;
  border-top: 10px solid transparent;
  border-left: 15px solid #3695cc;
  border-bottom: 10px solid transparent;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
}
.kiko-tool-tip.top .arrow {
  display: inline-block;
  position: absolute;
  content: "";
  width: 0;
  height: 0;
  left: 50%;
  bottom: -10px;
  border-top: 15px solid #3695cc;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}
.kiko-tool-tip.bottom .arrow {
  display: inline-block;
  position: absolute;
  content: "";
  width: 0;
  height: 0;
  left: 50%;
  top: -10px;
  border-bottom: 15px solid #3695cc;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}
